<template>
  <div id="post-wrapper">
    <post-card v-for="card in cards"
    :key="card.headerText"
    :headerText="card.headerText"
    :bodyText="card.bodyText"
    :mainColor="card.mainColor"
    :imgPath="card.imgPath"
    :routingPath="card.routingPath"
    />
  </div>
</template>

<script>
  import PostCard from '@/components/PostSection/PostCard'

  export default {
    name: 'PostSection',
    components: {
      PostCard
    },
    data: function () {
      return {
        cards: [
          {
            headerText: 'Notice',
            bodyText: '',
            bodySubText: '  ',
            mainColor: 'rgb(246, 99, 81)',
            imgPath: require('../../assets/icon/ic_notice/ic_notice.png'),
            routingPath: 'notice'
          },
          {
            headerText: 'Rule',
            bodyText: ' ',
            bodySubText: '   ',
            mainColor: 'rgb(255, 188, 71)',
            imgPath: require('../../assets/icon/ic_notice/ic_rule.png'),
            routingPath: 'rule'
          },
          {
            headerText: 'Question',
            bodyText: ' ',
            bodySubText: '    ',
            mainColor: 'rgb(53, 147, 211)',
            imgPath: require('../../assets/icon/ic_notice/ic_question.png'),
            routingPath: 'faq'
          }
        ]
      }
    }
  }
</script>

<style>
  /*post-section*/

  @media only screen and (min-width: 1271px){
    #post-wrapper {
      height: 70vh;
    }
  }

  @media only screen and (max-width: 1270px){
    #post-wrapper {
      height: 600px;
    }
  }

  #post-wrapper {
    font-family: 'NanumSquareRoundB';
    width: 100%;
    background-color: rgb(226, 226, 226);
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .post-card-wrapper {
    width: calc(100%/3);
    height: 100%;
    float: left;
  }

  .post-card {
    width: 90%;
    height: 100%;
    margin: 0 auto;
    cursor: pointer;
    will-change: transform;
  }

  .post-card:hover {
    -webkit-transform: scale(1.05);
    /*   */
    -moz-transform: scale(1.05);
    /* FireFox */
    -o-transform: scale(1.05);
    /* Opera */
    transform: scale(1.05);
    transition: transform .35s;
    -o-transition: transform .35s;
    -moz-transition: transform .35s;
    -webkit-transition: transform .35s;
  }

  .post-card-header {
    width: 100%;
    height: 15%;
    display: table;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
  }

  .post-card-header-text {
    text-align: center;
    color: white;
    font-size: 30px;
    display: table-cell;
    vertical-align: middle;
  }

  .post-card-body-wrapper {
    width: 100%;
    height: 85%;
    background-color: white;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    display: table;
  }

  .post-card-body {
    display: table-cell;
    vertical-align: middle;
  }

  .post-card-img-wrapper {
    width: 35%;
    margin: 0 auto;
  }

  .post-card-img {
    width: 100%;
    margin: 0 auto;
  }

  .post-card-body-text-wrapper:nth-child(n) {
    text-align: center;
    font-weight: bold;
  }

  .post-card-body-text {
    font-size: 30px;
    margin: 15px;
  }

  .post-card-body-sub-text {
    font-size: 15px;
  }
</style>



