<template>
  <div id="dev-intro-developer-wrapper">
    <dev-category :key="index" v-for="(data, index) in category" :data="data"/>
  </div>
</template>

<script>
import DevCategory from '@/components/FooterSection/DevCategory'

export default {
  name: 'DeveloperIntro',
  components: {DevCategory},
  data: function () {
    return {
      category: [
        {title: 'App', members: ['', '', '', '']},
        {title: 'Server', members: ['', '', '']},
        {title: 'Web Front', members: ['', ' ', '', '', '', '']},
        {title: 'Desktop', members: ['', '', '', '', '']},
        {title: 'Design', members: ['', '']}
      ]
    }
  }
}
</script>

<style>
</style>



